.footer-hint {
  font-weight: 500;
  font-size: 14px;
  color: #989CA8;
  letter-spacing: 0;
}
.footer-cta {
  cursor: pointer;
  font-weight: 500;
  font-size: 16px;
  color: #0053FA;
  letter-spacing: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 5px 0 0;
}
.footer-cta svg {
  margin-right: 5px;
}
.footer-cta span {
  text-decoration: underline;
}

